<template><section><html><head></head><body><h1>Model</h1>
<p>The <code v-pre="">vc-primitive-model</code> component is used to load a 3D model based on glTF, the runtime asset format for WebGL, OpenGL ES, and OpenGL.</p>
<h2>Example</h2>
<h3>Load a model with <code v-pre="">vc-primitive-model</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive-model :url="url" @ready-promise="readyPromise" :model-matrix="modelMatrix" :scale="10000" :minimum-pixel-size="128" :maximum-scale="200000" @click="clicked" :luminance-at-zenith="luminanceAtZenith" :specular-environment-maps="specularEnvironmentMaps" :spherical-harmonic-coefficients="sphericalHarmonicCoefficients">
        </vc-primitive-model>
        <div class="demo-tool">
          <span>Luminance at Zenith</span>
          <vue-slider v-model="luminanceAtZenith" :min="0" :max="2" :interval="0.01"></vue-slider>
          <md-checkbox v-model="proceduralEnvironmentLighting" class="md-primary">Use procedural environment lighting</md-checkbox>
        </div>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
