<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code> The url to the .gltf file.</td>
</tr>
<tr>
<td>basePath</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> The base path that paths in the glTF JSON are relative to.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if the model primitive will be shown.</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The 4x4 transformation matrix that transforms the model from model to world coordinates.</td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A uniform scale applied to this model.</td>
</tr>
<tr>
<td>minimumPixelSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> The approximate minimum pixel size of the model regardless of zoom.</td>
</tr>
<tr>
<td>maximumScale</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The maximum scale for the model.</td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> A user-defined object to return when the model is picked with Scene#pick.</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> When true, each glTF mesh and primitive is pickable with Scene#pick.</td>
</tr>
<tr>
<td>incrementallyLoadTextures</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determine if textures may continue to stream in after the model is loaded.</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if model WebGL resource creation will be spread out over several frames or block until completion once all glTF files are loaded.</td>
</tr>
<tr>
<td>clampAnimations</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if the model's animations should hold a pose over frames where no keyframes are specified.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> Determines whether the model casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4</strong></td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> For debugging only. Draws the bounding sphere for each DrawCommand in the model.</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> For debugging only. Draws the model in wireframe.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Determines how the model is drawn relative to terrain. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>scene</td>
<td>Object</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Must be passed in for models that use the height reference property.</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The condition specifying at what distance from the camera that this model will be displayed. <strong>Structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> A color that blends with the model's rendered color.</td>
</tr>
<tr>
<td>colorBlendMode</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Defines how the color blends with the model. <strong>HIGHLIGHT: 0, REPLACE: 1, MIX: 2</strong></td>
</tr>
<tr>
<td>colorBlendAmount</td>
<td>Number</td>
<td><code v-pre="">0.5</code></td>
<td><code v-pre="">optional</code> Value used to determine the color strength when the colorBlendMode is MIX. A value of 0.0 results in the model's rendered color while a value of 1.0 results in a solid color, with any value in-between resulting in a mix of the two.</td>
</tr>
<tr>
<td>silhouetteColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'RED'</code></td>
<td><code v-pre="">optional</code> The silhouette color. If more than 256 models have silhouettes enabled, there is a small chance that overlapping models will have minor artifacts.</td>
</tr>
<tr>
<td>silhouetteSize</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> The size of the silhouette in pixels.</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The ClippingPlaneCollection used to selectively disable rendering the model.</td>
</tr>
<tr>
<td>dequantizeInShader</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if a Draco encoded model is dequantized on the GPU. This decreases total memory usage for encoded models.</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> Scales diffuse and specular image-based lighting from the earth, sky, atmosphere and star skybox.</td>
</tr>
<tr>
<td>lightColor</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> The light color when shading the model. When undefined the scene's light color is used instead.</td>
</tr>
<tr>
<td>luminanceAtZenith</td>
<td>Number</td>
<td><code v-pre="">0.2</code></td>
<td><code v-pre="">optional</code> The sun's luminance at the zenith in kilo candela per meter squared to use for this model's procedural environment map.</td>
</tr>
<tr>
<td>sphericalHarmonicCoefficients</td>
<td>Array|Object</td>
<td></td>
<td><code v-pre="">optional</code> The third order spherical harmonic coefficients used for the diffuse color of image-based lighting.</td>
</tr>
<tr>
<td>specularEnvironmentMaps</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> A URL to a KTX file that contains a cube map of the specular lighting and the convoluted specular mipmaps.</td>
</tr>
<tr>
<td>credit</td>
<td>Object|String</td>
<td></td>
<td><code v-pre="">optional</code> A credit for the model, which is displayed on the canvas.</td>
</tr>
<tr>
<td>backFaceCulling</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Whether to cull back-facing geometry. When true, back face culling is determined by the material's doubleSided property; when false, back face culling is disabled. Back faces are not culled if Model#color is translucent or Model#silhouetteSize is greater than 0.0.</td>
</tr>
</tbody>
</table>
